                "\n" +
                "i wuv u so so so so so so so much oxi og <3 muah\n" +
                "i have had multiple sexual intercourses with your mother, she's hot\n" +
                "i'm also a big fan of ", document.create.a("https://www.youtube.com/watch?v=dQw4w9WgXcQ").also {
                    it.innerText = "garfield"
        })
    })
}